@import '../../variables';
@import '../../mixins';

header {
    position: relative;
    background-color: $themePewterBlue; 
}

#name {
    padding-top: 100px;
    position: absolute;
    text-align: center;
    top: 25%;
    width: 100%;
    z-index: 1;
}

#img-container img {
    object-fit: cover;
    width: 100%;
    max-height: 400px;
    position: relative;
    opacity: 0.4;
}